import { BaseRoutes } from '../../../routes/baseRoutes';

import { signUp, login } from '../controllers/index';

class AuthRoutes extends BaseRoutes {
  public constructor() {
    super({ mergeParams: true });
    this.routes();
  }

  public routes(): void {
    this.router.post('/signup', [], signUp);
    this.router.post('/login', [], login);
  }
}

export { AuthRoutes };
